<template>
  <div class="container position-absolute w-100 demo_container"></div>
  <div class="choose_bar container">
    <div class="leftBtn d-none">
      <img src="@/assets/LeftBtn.png" />
    </div>
    <template v-for="(item, index) in datas" :key="item.id">
      <picture :class="'imgBox container pic_' + index">
        <!-- index == 1 ? 'imgBox container show' : 'imgBox container' -->
        <img :src="item.banner_url" />
      </picture>
    </template>
    <div class="rightBtn d-none">
      <img src="@/assets/RightBtn.png" />
    </div>
  </div>
</template>

<script>
// import axios from "axios";
import $ from "jquery";
export default {
  props: {
    datas: {
      default: function () {
        var ary = [
          {
            id: 3,
            type: "game",
            banner_url:
              "https://mura-gaming-dev.s3.ap-northeast-1.amazonaws.com/3_1ce3e2df31.jpg",
            started_at: 1667433600,
            ended_at: 1668297600,
            game_id: 1,
          },
          {
            id: 2,
            type: "game",
            banner_url:
              "https://mura-gaming-dev.s3.ap-northeast-1.amazonaws.com/2_913fb40a08.jpg",
            started_at: 1667347200,
            ended_at: 1668211200,
            game_id: 1,
          },
          {
            id: 1,
            type: "game",
            banner_url:
              "https://mura-gaming-dev.s3.ap-northeast-1.amazonaws.com/1_0f504473ea.jpg",
            started_at: 1667260800,
            ended_at: 1668124800,
            game_id: 1,
          },
        ];
        return ary;
      },
      type: Object,
    },
  },
  data() {
    return {};
  },
  updated() {
    let nShowIndex = 1;
    let $chooseBar = $(".choose_bar");
    let a$imgs = $chooseBar.find(".imgBox");
    let $ShowImg = $($chooseBar.find("img")[nShowIndex]);
    let nConMargin =
      ($chooseBar.outerWidth(true) - $chooseBar.outerWidth()) / 2;
    nConMargin = nConMargin > 200 ? 200 : nConMargin;
    $chooseBar.height($ShowImg.height());
    let nConLeft = 0;

    for (let i = 0; i < a$imgs.length; i++) {
      let $pic = $(a$imgs[i]);
      if (i == nShowIndex - 1) {
        $pic.removeClass("centerImg");
        $pic.css("left", -nConMargin + "px");
        $pic.css("transform-origin", "left bottom");
      } else if (i == nShowIndex) {
        $pic.addClass("centerImg");
        $pic.css("left", nConLeft + "px");
      } else if (i == nShowIndex + 1) {
        $pic.removeClass("centerImg");
        $pic.css("right", -nConMargin + "px");
        $pic.css("transform-origin", "right bottom");
      }
    }

    // console.log($chooseBar);
  },
};
</script>

<style scoped lang="scss">
.choose_bar {
  width: 100%;
  position: relative;
  .centerImg {
    z-index: 9;
    > img {
      box-shadow: 0px 3px 5px 0px #6e6e6e;
    }
  }
  .imgBox {
    width: 100%;
    position: absolute;
    top: 0;
    img {
      cursor: pointer;
      -webkit-transition: all 0.5s;
      -moz-transition: all 0.5s;
      -o-transition: all 0.5s;
      transition: all 0.5s;
      min-width: 100%;
      &:hover {
      }
    }
    &:not(.centerImg) {
      transform: scale(0.9);
      opacity: 0.6;
    }
  }
  .leftBtn {
    position: absolute;
    left: 0;
    z-index: 99;
  }
  .rightBtn {
    position: absolute;
    z-index: 99;
    right: 0;
  }
}

@media (max-width: 1200px) {
}
</style>
